import { noop, isFunction, shallowEqual, extend } from './util';

const connect = (mapStateToProps = noop) => {
  let app = getApp();

  return (params) => {
    let commponentDidMount = params.didMount;
    let commponentDidUnMount = params.didUnmount;
    let idx;

    const didMount = () => {
      let store = this.store = app.store;

      if (store && store.subscribe) {
        idx = store.subscribe(setState.bind(this));
        setState.call(this);
      }
      if (isFunction(commponentDidMount)) {
        commponentDidMount.call(this);
      }
    };

    const didUnmount = () => {
      if (isFunction(commponentDidUnMount)) {
        commponentDidUnMount.call(this);
      }
      store.unSubscrible(idx);
    };

    const setState = () => {
      const state = mapStateToProps(this.store.state);
      if (!shallowEqual(this.data, state)) {
        this.setData(state);
      }
    }

    return {
      ...params,
      didMount,
      didUnmount
    };
  }
}

export default connect;